.index {
    .content {
        padding-top: 77px;
        .contact {
            padding: 70px 120px;
            background-color: #383d51;
            color: #fff;
            .title {
                margin-right: 100px;
                font-size: 30px;
                font-weight: bold;
            }
        }
        .business {
            margin-top: 50px;
            .title {
                font-size: 30px;
                font-weight: bold;
            }
            .sub-title {
                margin-top: 15px;
                color: #666;
                p {
                    margin-bottom: 0;
                    line-height: 1.8;
                }
            }
            .b-img {
                margin-top: 15px;
            }
        }
        .suc-case {
            margin-top: 50px;
            .title {
                font-size: 30px;
                font-weight: bold;
                text-align: center;
            }
            .box {
                padding: 50px 120px;
                .item {
                    text-align: center;
                    border: 1px solid #e2e2e2;
                    padding: 30px 15px;
                    border-radius: 10px;
                    margin-right: 30px;
                    min-height: 170px;
                    .title {
                        font-size: 18px;
                        font-weight: bold;
                    }
                    .sub-tit {
                        font-size: 15px;
                        height: 50px;
                    }
                    &:last-child {
                        margin-right: 0;
                    }
                    // img {
                    //     width: auto;
                    //     height: 100px;
                    // }
                    
                    transition: all .15s linear;
                    &:hover {
                        border-color: rgba(0, 0, 0, 0.65);
                    }
                    &.item-middle {
                        margin-left: 50px;
                    }
                    .img-box {
                        img {
                            width: 30px;
                            margin-right: 10px;
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }
        .reason {
            margin-top: 50px;
            padding: 0 120px 100px;
            .title {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 30px;
            }
            .text {
                font-size: 18px;
            }
            .sub-title {
                margin-bottom: 10px;
                font-weight: bold;
                font-size: 18px;
            }
            .img-outer {
                margin-right: 80px;
                img {
                    width: 100%;
                }
            }
        }
    }
}
